.text-muted {
    color: $base-muted-color;
}

.disable-scrollbar {
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE 10+ */

    &::-webkit-scrollbar {
        background: transparent; /* Chrome/Safari/Webkit */
        width: 0px;
        height: 0;
    }
}

// List
.clean-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.inline-clamp {
    display: -webkit-box;
    overflow: hidden;
    word-break: break-all;
    text-overflow: ellipsis;
    line-height: inherit;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}
.text-bold {
    font-weight: 600;
}
.text-light {
    font-weight: 300;
}

// Inline space
.space-inline-s {
    & > *:not(:last-child) {
        margin-inline-end: 1em;
    }
}

.space-inline-m {
    & > *:not(:last-child) {
        margin-inline-end: 2em;
    }
}

.space-inline-l {
    & > *:not(:last-child) {
        margin-inline-end: 3em;
    }
}

// Flex

.flex {
    display: flex;

    &.column {
        flex-direction: column;
    }

    &.flex-center {
        align-items: center;
        justify-content: center;
    }

    &.justify-between {
        justify-content: space-between;
    }

    &.justify-center {
        justify-content: center;
    }

    &.justify-start {
        justify-content: flex-start;
    }

    &.justify-end {
        justify-content: flex-end;
    }

    &.align-center {
        align-items: center;
    }
}

.cursor-pointer {
    cursor: pointer;
}
